import styled from "styled-components";

const AccountStyles = styled.div`
  max-width: calc(${props => props.theme.maxWidth} - 5%);
  margin: 0 auto;
  padding: 0 2.5%;
  h3 {
    text-align: center;
    color: ${props => props.theme.semiblack};
    font-size: 190%;
  }
  .permissions-block {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
`;

export default AccountStyles;
